class Utils {
  // Get the contentWindow of the iframe
  postMessageToIframeWindow (...Args) {
    let iframe = document.getElementById('inject-litmus-checklist-iframe');
    if ( iframe && iframe.contentWindow){
      iframe.contentWindow.postMessage(...Args);
    }
  }

  recordPageAction(action, attributes) {
    this.postMessageToIframeWindow({ method: 'checklist-recordPageAction', action, attributes }, '*');
  }
}

module.exports = new Utils();


