const jQuery = require('jquery');

class ChecklistTestPageProvider {

  constructor() {
    this.editable = false;
  }

  lookup(options = {}) {
    let content   = jQuery('#htmlContent').val();
    let text      = jQuery('#textContent').val();
    let subject   = jQuery('#subject').val();
    let fromName  = jQuery('#name').val();
    let fromEmail = jQuery('#email').val();

    let result = {content, text, subject, fromName, fromEmail};

    window.postMessage({
      method: 'checklist-lookupResult',
      result: result,
      testType: (options.testType || 'checklist')
    }, '*');
  }
}

window.ChecklistProvider = new ChecklistTestPageProvider();



