export class ApiService {
  constructor(proto, baseHost, baseUrl) {
    this.proto     = proto
    this.baseHost  = baseHost
    this.baseUrl   = baseUrl

    this.addConvenienceMethods();
  }

  addConvenienceMethods() {
    let requestTypes = ['GET', 'POST', 'PUT'];

    for (var i = 0; i < requestTypes.length; i++) {
      let requestType = requestTypes[i];
      this[`${requestType.toLowerCase()}ToApi`] = function(url, opts = {}) {
        let authToken    = opts['authToken'];
        let mfaRequired  = opts['mfaRequired'];
        let data         = opts['data'] || {};
        let success      = opts['successCallback'];
        let error        = opts['errorCallback'];
        let asXHR        = opts['asXHR'];
        let username     = opts['username'];
        let password     = opts['password'];
        let headers      = opts['headers'];
        let dataType     = opts['dataType'];

        let options = {
          type: requestType,
          dataType: (dataType || 'json'),
          headers: headers
        };

        options.url  = `${url}.json`;

        if (authToken) {
          options.headers = options.headers || {};
          options.headers['Authorization'] = 'Basic ' + btoa(authToken + ':');
        } else if(username && password) {
          let authorization = btoa(`${username}:${password}`);
          options.headers = options.headers || {};
          options.headers['Authorization'] = `Basic ${authorization}`;
        }
        if (data && !(Object.keys(data).length === 0 && data.constructor === Object)) {
          options.data = data;
        }

        if (asXHR) {
          options.headers = options.headers || {};
          options.headers['X-Requested-With'] = 'XMLHttpRequest';
        }

        if (mfaRequired) {
          chrome.runtime.sendMessage({
            type: 'storage_get',
            data: ['mfaToken']
          },
          ((items) => {
              let mfaToken = items.mfaToken;
              options.headers = options.headers || {};
              options.headers['X-Litmus-OTP'] = mfaToken;

              this.backgroundJQueryAjax(options, success, error);
            })
          );
        } else {
          this.backgroundJQueryAjax(options, success, error);
        }

      };
    }
  }

  dismissFileAccessNotice(successCallback = null, errorCallback = null) {
    data = {
      file_access_dismissed: true
    };
    let url = this.v2Url("accounts/dismiss_file_access")
    this.postToApi(url, { data, mfaRequired: true, successCallback, errorCallback })
  }

  login(username, password, successCallback, errorCallback) {
    let url = this.v2Url("accounts")
    this.getToApi(url, { username, password, mfaRequired: false, asXHR: true, successCallback, errorCallback });
  }

  checkLoginState(successCallback, errorCallback) {
    let url = this.v2Url("accounts")
    this.getToApi(url, { headers: { 'Authorization': 'Basic ' }, successCallback, errorCallback })
  }

  getESPResults(data, successCallback, errorCallback, authToken, currentESP) {
    // this method should no longer be called when logged out or in a non-paid plan,
    // since free plans are depreciated and we now show a cta page instead.  Return early if
    // something slips through the cracks
    if (authToken === null) {
      return;
    }

    let id = data['id'];
    delete data['id'];

    data = {
      email: data,
      esp_uid: `${currentESP.code}-${id}`,
      esp: currentESP.api_code
    };

    let url = `${this.baseUrl}/folders/external_api/emails`;
    this.postToApi(url, { data, authToken, mfaRequired: true, successCallback, errorCallback });
  }

  requestESPScreenshots(token, testId, successCallback, errorCallback) {
    // this method should no longer be called when logged out or in a non-paid plan,
    // since free plans are depreciated and we now show a cta page instead.  Return early if
    // something slips through the cracks
    if (token === null) {
      return;
    }

    if (typeof successCallback === 'undefined') { successCallback = function(){ } }
    if (typeof errorCallback === 'undefined') { errorCallback = function(){ } }

    let headers   =   {};
    let url       = null;
    let tokenHash = null;

    chrome.runtime.sendMessage({
        type: 'storage_get',
        data: ['mfaToken']
      },
      ((items) => {
        tokenHash = btoa(token + ':');
        headers['Authorization'] = `Basic ${tokenHash}`;
        headers['X-Litmus-OTP']  = items.mfaToken;

        let url = this.checklistUrl(`tests/${testId}/render`);
        this.putToApi(url, { dataType: 'text', headers: headers, successCallback, errorCallback })
      })
    );
  }

  getLocalFileTestResults(data, successCallback, errorCallback, authToken) {
    // this method should no longer be called when logged out or in a non-paid plan,
    // since free plans are depreciated and we now show a cta page instead.  Return early if
    // something slips through the cracks
    if (authToken === null) {
      return;
    }

    data = {
      email: data,
      force_token_auth: true
    };
    let url = this.checklistUrl("local_tests");
    this.postToApi(url, { data, authToken, mfaRequired: true, successCallback, errorCallback });
  }

  v2Url(endpoint) {
    return `${this.proto}api.${this.baseHost}/v2/${endpoint}`
  }

  checklistUrl(endpoint) {
    return `${this.proto}${this.baseHost}/checklist/external_api/${endpoint}`;
  }

  backgroundJQueryAjax(options, successCallback, errorCallback) {
    chrome.runtime.sendMessage(
      { type: 'background_jquery_ajax', data: options },

      function(response) {
        if(response == null) {
          errorCallback(null, null, "No response from AJAX call to: ${options.url}" )
          return;
        }

        // we don't receive a full jqXHR object back from the chrome
        // background script, so we need to re-assemble some of it for
        // existing code that is expecting to receive an jqXHR object.
        response.jqXHR.getAllResponseHeaders = function() {
          return response.headers;
        };

        response.jqXHR.getResponseHeader = function(key) {
          return response.headers[key];
        };

        if(response.success) {
          successCallback(response.data, response.textStatus, response.jqXHR)
        } else {
          errorCallback(response.jqXHR, response.textStatus, response.errorThrown)
        }
      }
    );
  }
}



