import providers from "./providers.json";

class Provider {
  acousticName() {
    return providers["cloud.goacoustic.com"].name;
  }

  adobeName() {
    return providers["experience.adobe.com"].name;
  }

  eloquaName() {
    return providers["eloqua.com"].name;
  }

  ibmName() {
    return providers["ibm.com"].name;
  }

  klaviyoName() {
    return providers["klaviyo.com"].name;
  }

  pardotName() {
    return providers["pi.pardot.com"].name;
  }
}

module.exports = new Provider();



