const jQuery = require('jquery');

class SendgridProvider {

  constructor() {
    this.editable = false;
  }

  lookup(options = {}) {
    let previewURL = window.location.href.replace('edit', 'preview') + '.json';

    jQuery.get(previewURL).done((data) => {
      let result = {
        content: data.html_content,
        text: data.plain_content,
        subject: data.subject,
        fromName: null,
        fromEmail: null
      };

      window.postMessage({
        method: 'checklist-lookupResult',
        result: result,
        testType: (options.testType || 'checklist')
      }, '*');
    });
  }

  injectCSS() {
    jQuery('body').append(`
      <style id='checklist-injected-css'>
        .content-editor-container .left-action {
          padding-right: 20px;
          padding-left: 20px;
        }

        .modal-dialog {
          margin-left: inherit;
        }

        .cke_dialog.cke_browser_webkit.cke_ltr.cke_single_page {
          left: 200px!important;
        }
      </style>
    `);
  }
}

window.ChecklistProvider = new SendgridProvider();



