const jQuery = require('jquery');

class EmmaProvider {
  constructor() {
    this.editable = false;
    this.id = this.getCampaignId;
  }

  getCampaignId () {
    // Example: app2/campaigns/review_send/209207486/
    let idMatch = window.location.href.match(/app2\/campaigns\/[^/]+\/([0-9]+)/);
    if (idMatch) {
      return idMatch[1];
    } else {
      return null;
    }
  }

  lookup(options = {}) {
    let campaignId = this.getCampaignId();
    let previewPath = `/app2/campaigns/preview_iframe/${campaignId}`;

    jQuery.get(previewPath).done( (html) => {
      // Emma injects script tags inside the iframe HTML head,
      // so we pass script tests via decapitation
      html = html.replace(
        /<head>[\s\S]*<\/head>/, '');

      let result = {
        content: html,
        text: '',
        subject: '',
        fromName: null,
        fromEmail: null
      };
      let getDataFn = null;
      if (window.location.pathname.indexOf('campaigns/review_send') !== -1){
        getDataFn = this.fetchDataFromCurrentWindow();
      } else {
        getDataFn = this.fetchReviewPageData();
      }

      getDataFn( (data) => {
        result = jQuery.extend(result, data);

        window.postMessage({
          method: 'checklist-lookupResult',
          result: result,
          testType: (options.testType || 'checklist')
        }, '*');
      });
    });
  }

  fetchDataFromCurrentWindow() {
    return (callback) => {
      let data = {};

      if (jQuery('#review-form-subject').is(':visible')){
        data.subject = jQuery('#review-form-subject').val();
      } else {
        // Emma allows users to split-test subjects.
        // Just use variation A for now.
        data.subject = jQuery('#review-form-subject-a').val();
      }

      data.fromName  = jQuery('#review-form-from-name').val();
      data.fromEmail = jQuery('#review-form-from-email').val();

      callback(data);
    };
  }


  fetchReviewPageData(callback) {
   return (callback) => {
    campaignId = this.getCampaignId();
    reviewDataPath = `/app2/campaigns/edit/campaigns/${campaignId}/`;
    jQuery.get(reviewDataPath).done( (response) => {
      let data = {};
      // This API call doesn't seem to know about subject split tests,
      // so subject is always a string.
      data.subject   = response.data.mailing.subject;
      data.fromName  = response.data.mailing.from.name;
      data.fromEmail = response.data.mailing.from.email;

      callback(data);
    });
   };
  }

  injectCSS() {
    jQuery('body').append(`
      <style id='checklist-injected-css'>
        #canvas-header .limit {
          min-width: 0px;
          padding: 0 8px;
        }

        #site {
          min-width: 0px;
        }

        #canvas-toolbar {
          margin-left: 145px;
          width: inherit;
          padding: 0px 10px 0px 10px;
        }

        #canvas-header {
          width: inherit!important;
        }

        .canvas-fixed #canvas-header {
          max-width: 955px;
        }

        .limit > #canvas-options {
          margin-left: 145px;
        }

        #mailing-create {
          padding-left: 9px;
          min-width: 0px;
          padding-right: 9px;
        }


        #viewport {
          margin-left: 145px;
        }

        #campaign-title {
          width: 32%;
        }

        #canvas-toolbar .button.secondary>a, #canvas-toolbar .button.tertiary>a, #canvas-toolbar .button.primary>a {
          font-size: 11px;
          padding-left: 8px;
          padding-right: 8px;
        }

        #preview {
          margin-right: 5px;
        }

        #save {
          margin-right: 5px;
        }

        #undo {
          margin-right: 5px;
        }

        .e2ma-content-block-helper {
          width: 100px;
        }

        .content-type-list {
          margin-left: 0px;
        }

        #site-head-wrap {
          width: 100%;
        }

       .ui-dialog.e2ma-modal-dialog {
          width: 50%!important;
          left:  100px!important;
       }


      input + #viewport {
        margin-left: 0px;
      }

      .limit + #canvas-options {
          margin-left: 0px;
        }

      #thumbnail-column {
        width: 250px;
      }

      #review_form_format_chzn, #review_form_signup_form_chzn {
        width: 250px;
      }

      #canvas-toolbar:only-child {
        margin-left: 0px!important;
      }

      #recipient-list .e2ma-token-input-wrapper .e2ma-token-label {
        bottom: 7px!important;
      }

      footer {
        padding-top: 70px!important;
      }
      </style>
    `);
  }
}

window.ChecklistProvider = new EmmaProvider();



