const jQuery = require('jquery');

export default class CampaignMonitorProvider {

  constructor() {
    this.editable = false;
    this.id = this.getCampaignId;
  }

  getCampaignId() {
    // Example: /campaigns/gallery/step2/AA84B178B26EAABF/s#import
    let idMatch = window.location.href.match(/step[0-9]\/([A-Za-z0-9]+)/);
    if (idMatch) {
      return idMatch[1];
    }

    // Example: /campaigns/createSend/snapshot.aspx?cID=AA84B178B26EAABF
    idMatch = window.location.href.match(/(cID|cid)=([A-Za-z0-9]+)/);
    if (idMatch) {
      return idMatch[2];
    }

    // Example: /campaigns/design/index/1e1eac4b0e2bf73?category=gallery-featured#/editor
    idMatch = window.location.href.match(/campaigns\/design\/index\/([A-Za-z0-9]+)/);
    if (idMatch) {
      return idMatch[1];
    }

    return null;
  }

  lookup(options = {}) {
    let entityId = this.getCampaignId();

    if (entityId) {
      let snapshotUrl = '/campaigns/createSend/snapshot.aspx?cID=' + entityId;
      let result      = {};

      const getPreviewPageFromSnapshot = (snapshotData) => {
        let $snapshotHTML = jQuery(snapshotData);
        let previewPopupURL = $snapshotHTML.find('a#htmlPreview').attr('href');
        if (previewPopupURL) {
          // Fetch the preview popup, which embeds an iframe containing the HTML email
          return jQuery.get(previewPopupURL);
        }

        return jQuery.Deferred().reject('Could not find HTML preview link!');
      };

      const processSubjectAndSenderDetails = (snapshotData) => {
        let $snapshotHTML = jQuery(snapshotData);

        result.subject = $snapshotHTML.find('.primary-form div.col:contains("Subject")').next().text().trim();
        let fromNameAndEmail = $snapshotHTML.find('.primary-form div.col:contains("From")').next().text().trim();

        // Parse name and email from "Test User <test@example.com>"
        let nameEmailMatch = fromNameAndEmail.match(/([^<]+)<([^>]+)/);
        if (nameEmailMatch) {
          result.fromName  = nameEmailMatch[1].trim();
          result.fromEmail = nameEmailMatch[2].trim();
        }

       return getPreviewPageFromSnapshot(snapshotData);
      };

      const processPreviewPopup = (previewPopupData) => {
        let $popupHTML = jQuery(previewPopupData);

        let htmlPreviewURL = $popupHTML.find('iframe#htmlFrame').attr('src');
        if (htmlPreviewURL) {
          // Finally, fetch the HTML email from the iframe
          return jQuery.get(htmlPreviewURL);
        }
        return jQuery.Deferred().reject('Could not find HTML preview iframe!');

      };

      const processHTMLPreview = (htmlPreviewData) => {
            // Campaign Monitor injects two script tags which add target="_blank" to links in the preview,
            // so we need to remove those to avoid breaking the 'script' check.
            htmlPreviewData = htmlPreviewData.replace(
              /<script type="text\/javascript" src="https:\/\/js\.createsend.*<\/script>/, '');

            result.content = htmlPreviewData;
            result.text = '';
            result.id = this.id();

            window.postMessage({
              method: 'checklist-lookupResult',
              result: result,
              testType: (options.testType || 'checklist')
            }, '*');
      };

      if (this.isOnCampaignAndSenderDetailsPage()) {
        result.subject   = $('input#Subject').val();
        result.fromName  = $('input#FromName').val();
        result.fromEmail = $('input#fromEmailPrefix').val() + '@' + $('select#fromEmailDomain option[selected]').text();

        jQuery.get(snapshotUrl).pipe(getPreviewPageFromSnapshot)
        .pipe(processPreviewPopup)
        .pipe(processHTMLPreview)
        .fail((error) => {
          console.error(error);
        });
      } else {
      // Fetch the snapshot page, which contains subject, from name, and email
        jQuery.get(snapshotUrl).pipe(processSubjectAndSenderDetails)
        .pipe(processPreviewPopup)
        .pipe(processHTMLPreview)
        .fail((error) => {
          console.error(error);
        });
      }
    } else {
      console.error('Checklist Extension', 'Can\'t find entity id');
    }
  }

  isOnCampaignAndSenderDetailsPage() {
    let match = window.location.href.match(/step1\/([A-Za-z0-9]+)/);
    return match !== null;
  }

  opened() {
    jQuery('#emb-js-editor-header, #emb-editor-wrap').css('width', 'calc(100% - 470px)');
  }

  closed() {
    jQuery('#emb-js-editor-header, #emb-editor-wrap').css('width', 'inherit');
  }

  injectCSS() {
    jQuery('body').append(`
      <style id='checklist-injected-css'>
        .wrap {
          min-width: 0;
        }

        .wrap.content {
          width: 90%;
          padding: 0 5%;
        }

        .l-container {
          min-width: 0;
        }

        #emb-body .emb-editor-header {
          right: 0;
        }

        .primary-form-wrap.expanded > ul.primary-form { width: 60%!important; }

        .l-grid.l-grid--gutter-huge {
          margin-left: -14rem!important;
        }

        .litmus-checklist-expanded .l-container {
          font-size: 10px;
        }

        .litmus-checklist-expanded .c-site-header__nav-main__link {
          padding-left: 0.7rem;
          padding-right: 0.7rem;
        }

        .galleryv2 .primary-form-wrap.expanded .primary-form textarea {
          width: calc(100% - 21px);
        }

        @media screen and (max-width: 1320px) {
          .litmus-checklist-expanded .l-container {
            font-size: 9px;
          }
        }

        .primary-form-wrap.message {
          width: 100%;
        }

        a#editContent {
          right: 0;
        }

        a#editCampaign {
          right: 0;
        }

        a#addMoreRecipients {
          right: 0;
        }

        #regular-content > .primary-form {
          width: 100%!important;
          margin: auto;
        }

        input#Name {
          width: calc(100% - 25px);
        }

        input#Subject {
          width: calc(100% - 25px);
          padding-right: 13px!important;
        }

        input#FromName {
          width: inherit;
        }

        input#fromEmailPrefix {
          width: inherit;
        }


        form#step4 > ul.primary-form {
          width: 100%;
        }
      </style>
    `);

    // @media screen and (max-width: 1400px) {
    //   .primary-form-wrap { width: 756px; }
    //   .primary-form { width: 724px; }
    // }
  }

  getAccountInfo(callback) {

    const parseAccountInfo = (page) => {
      let result = page.match(/(personData =)\s+(\[[^;]*);/);
      let username = null;
      let email    = null;
      let personData = null;
      if(result && result[2]){
        personData = JSON.parse(result[2]);
        email = personData[0].Email;
        username = personData[0].Name;
      }
      if (personData) {
        return {email: email, username: username};
      } else {
        return null;
      }
    };

    const success = (email, username) => {
      callback({
        name: 'CreateSend',
        username: username,
        email: email,
        campaign_url: `${window.location.origin}/campaigns`
      });
    };

    const tryClientAccount = () => {
      let clientAccessAccountUrl = window.location.origin + '/settings/';
      jQuery.get(clientAccessAccountUrl).done( (page) => {
        let acc = parseAccountInfo(page);
        if (acc !== null) {
          success(acc.email, acc.username);
        } else {
          console.log('Failed to Retrive Account Page from url', clientAccessAccountUrl);
        }
      } ).fail((error) => {
        console.log('Failed to Retrieve Account Page from url', clientAccessAccountUrl);
      });
    };

    const tryNormalAccount = () => {
      let normalAccountUrl = window.location.origin + '/team/';
      jQuery.get(normalAccountUrl).done( (page) => {
        let acc = parseAccountInfo(page);
        if (acc !== null) {
          success(acc.email, acc.username);
        } else {
          console.log('Failed to Retrive Account Page from url', normalAccountUrl);
          tryClientAccount();
        }
      } ).fail((error) => {
        console.log('Failed to Retrieve Account Page from url', normalAccountUrl);
        tryClientAccount();
      });
    };

    let adminAccountUrl = window.location.origin + '/admin/account/';
    jQuery.get(adminAccountUrl).done( (page) => {
      let acc = parseAccountInfo(page);
      if (acc !== null) {
        success(acc.email, acc.username);
      } else {
        console.log('Failed to Retrive Account Page from url', adminAccountUrl);
        tryNormalAccount();
      }
    } )
    .fail( (error) => {
      console.log('Failed to Retrive Account Page from url', adminAccountUrl);
      tryNormalAccount();
    });
  }
}

window.ChecklistProvider = new CampaignMonitorProvider();



