const jQuery = require('jquery');

class SilverpopProvider {

  constructor() {
    this.editable = false;
  }

  lookup(options = {}) {
    let lookupFn = null;
    if (window.location.pathname.indexOf('composeMailing7.do') === -1) {
      lookupFn = this.downloadComposeHTML();
    } else {
      lookupFn = this.fetchHTMLFromCurrentPage();
    }

    lookupFn((html) => {
      let content = html.find('#fckTextArea___Frame').contents().find('#xEditingArea').find('iframe').contents().find('body').html() 
                      || html.find('#fckTextArea___Frame').contents().find('#xEditingArea').find('textarea').val()
                      || html.find('#mailingHTMLBody').val();

      //let content   = html.find('#mailingHTMLBody').val();
      let text      = html.find('#mailingTEXTBody').val();
      let subject   = html.find('#mailingSubject').val();
      let fromName  = html.find('#mailingFromName').val();
      let fromEmail = html.find('#mailingFromAddress').val();
      let id        = this.id();

      let result = {content, text, subject, fromName, fromEmail, id};

      window.postMessage({
        method: 'checklist-lookupResult',
        result: result,
        testType: (options.testType || 'checklist')
      }, '*');
    });
  }

  fetchHTMLFromCurrentPage() {
    return (callback) => {
      callback(jQuery('body'));
    };
  }

  downloadComposeHTML() {
    return (callback) => {
      let thumbHolder = jQuery('.templateThumbnailHolder').eq(0);

      if (thumbHolder.length) {
        let mailingId = null;
        let matchData = thumbHolder.attr('style').match(/MAILING_ID=(\d+)/);
        if (matchData) {
          mailingId = matchData[1];
          composeURL = `/composeMailing7.do?action=displayLoadTemplate&mailingid=${mailingId}&templateType=0`;

          jQuery.get(composeURL).done((data) => {
            callback(jQuery(data));
          });
        } else {
          console.log('Could not find mailing ID in .templateThumbnailHolder style!');
        }
      } else {
        let matchData = window.location.href.match(/.\/composer\/(\d+)(\/.)*/);
        if (matchData) {
          let mailingId = matchData[1];
          composeURL = `/composeMailing7.do?action=displayLoadTemplate&mailingid=${mailingId}&templateType=0`;

          jQuery.get(composeURL).done((data) => {
            callback(jQuery(data));
          });
        }
      }
    };
  }

  id() {
    let idMatch = window.location.href.match(/mailingid=(\d+)/) || window.location.href.match(/.\/composer\/(\d+)(\/.)*/);
    if (idMatch) {
      return idMatch[1];
    }
  }
}

window.ChecklistProvider = new SilverpopProvider();



