const jQuery = require('jquery');
const ChecklistTester = require('../../chrome/inject/modules/checklistTester');

export default class MarketoProvider {
  constructor() {
    this.editable   = false;
    this.subject    = null;
    this.name       = null;
    this.email      = null;
    this.html       = null;
    this.hasRun     = false;
  }

  lookup(options = {}) {
    this.hasRun    = false;
    this.html      = null;
    let campaignId = this.campaignId();
    let html,
        subject,
        fromName,
        fromEmail;

    this.fetchResultIfReady(options);

    // if #contentFrame is on page, make sure we wait until it's loaded
    if (jQuery('#contentFrame').length > 0 && !this.content()) {
      jQuery('#contentFrame').load(() => {
        this.fetchResultIfReady(options);
      });
    }

    if(campaignId) {
      var observer = new MutationObserver((mutations) => {
        mutations.forEach((mutation) => {
          if (mutation.addedNodes && mutation.addedNodes.length > 0) {
            let frame = jQuery('div[id*=emailEditorCanvas-]').filter('div[id*=-body]').find('iframe').first();
            if(jQuery('.CodeMirror')[0]) {
              // on code edit page, run immediately
              this.fetchResultIfReady(options);
            } else if(frame) {
              frame.load(() => {
                this.fetchResultIfReady(options);
              });
            }
          }
        });
      });

      var config = {
          attributes: true,
          childList: true,
          characterData: true,
          subtree: true
      };

      observer.observe(document.body, config);
    }
  }

  fetchResultIfReady(options={}) {
    if(this.content() && !this.hasRun) {
      if (jQuery('.CodeMirror')[0]) {
        this.fetchResult();
      } else {
        // if we're on the easy-edit page, replace dynamic image links
        this.replaceImages(this.content()).then((imageData) => {
          this.fetchResult(imageData, options);
        });
      }
      this.hasRun = true;
    }
  }

  fetchResult(imageData={}, options={}) {
    let result = {
        content: this.content(),
        text: ' ',
        subject: this.subjectLine(),
        fromName: this.fromName(),
        fromEmail: this.fromEmail(),
        imageData: imageData,
        id: this.campaignId() || this.id()
    };
    window.postMessage({
        method: 'checklist-lookupResult',
        result: result,
        testType: (options.testType || 'checklist')
    }, '*');
  }

  replaceImages(html) {
    let frame  = jQuery('div[id*=emailEditorCanvas-]').filter('div[id*=-body]').find('iframe').first();
    let images = frame.contents().find('html').find('img[src*="proxyAsset"]');
    let loaded_images = Array.apply(null, Array(images.length)).map(Number.prototype.valueOf,0);
    let data   = {};

    return new Promise(function(resolve, reject) {
      if (images.length > 0) {
        images.each((i) => {
          let image = images.eq(i);
          let path  = image.attr('src');
          let xhr = new XMLHttpRequest();
          xhr.responseType = 'blob';


          xhr.onload = function() {
            var reader = new FileReader();

            // run when image is converted to base64
            reader.onloadend = function() {
              let rpath = image.attr('src');
              data[rpath] = reader.result;
              loaded_images[i] = 1;

              // // if all images have been accounted for, send payload to qa external api to retrieve s3 paths
              if (loaded_images.every(elem => elem === 1)) {
                resolve(data);
              }
            };
            // read requested image as base64 path
            reader.readAsDataURL(xhr.response);
          };

          xhr.onerror = function() {

          };

          // request image via xhr
          ChecklistTester.requestImage(xhr, path);
        });
      } else {
        resolve([]);
      }
    });
  }

  campaignId() {
    let matchData = window.location.href.match(/\#EME(\d+)/) ||
      window.location.href.match(/\#EMTE(\d+)/) ||
      window.location.href.match(/\#templatePreview\?templateId=(\d+)/);
    if (matchData !== null) {
      return matchData[1];
    } else {
      return null;
    }
  }

  content() {
    let frame;
    if(this.html) {
      return this.html;
    }

    if (jQuery('.CodeMirror')[0]) {
      this.html = jQuery('.CodeMirror')[0].CodeMirror.getValue();
      return this.html;
    } else if(jQuery('#contentFrame').length > 0) {
      frame = jQuery('#contentFrame');
    } else {
      frames = jQuery('div[id*=emailEditorCanvas-]').filter('div[id*=-body]').find('iframe');
      frame  = frames.filter(':visible').first();
    }

    if(frame && frame.contents().find('html').html() !== '<head></head><body></body>') {
      this.html = frame.contents().find('html').html();
    }
    return this.html;
  }

  subjectLine() {
    if(this.subject) {
      return this.subject;
    }
    let subjectLabel = jQuery('label:contains(Subject:)').first();
    if(subjectLabel) {
      this.subject = subjectLabel.next().text();
    }
    if(jQuery('input[name="subject"]').length) {
      this.subject = jQuery('input[name="subject"]').val();
    }
    return this.subject;
  }

  fromName() {
    if(this.name) {
      return this.name;
    }
    let fromLabel = jQuery('label:contains(From Name:)').first();
    if(fromLabel) {
      this.name = fromLabel.next().text();
    }
    if(jQuery('input[name="fromName"]').length) {
      this.name = jQuery('input[name="fromName"]').val();
    }
    return this.name;
  }

  fromEmail() {
    if(this.email) {
      return this.email;
    }
    let addressLabel = jQuery('label:contains(From Address:)').first();
    if(addressLabel) {
      this.email = addressLabel.next().text();
    }
    if(jQuery('input[name="fromName"]').length) {
      this.email = jQuery('input[name="fromEmail"]').val();
    }
    return this.email;
  }

  id() {
    let matchData = window.location.search.match(/\#EM[T|E]*(\d*)/);
    if (matchData !== null) {
      return matchData[1];
    }
  }
}

window.ChecklistProvider = new MarketoProvider();


