const Litmus = require('../../../base/litmus');

class Accounts {
  constructor() {
    this.accountSynced = false;
  }

  // Get the contentWindow of the iframe
  postMessageToIframeWindow(...Args) {
    let iframe = document.getElementById('inject-litmus-checklist-iframe');
    if ( iframe && iframe.contentWindow){
      iframe.contentWindow.postMessage(...Args);
    }
  }

    // Update the current account index
  selectCurrentAccount(index) {
    Litmus.clearSession();

    chrome.runtime.sendMessage({
      type: 'storage_set',
      data: { current_account: index }
    }, () => {
      this.currentAccount((account) => {
        let accountSwitchUrl = `https://${Litmus.host()}/accounts/switch?account%5Bid%5D=${account.account_id}`;
        chrome.runtime.sendMessage({ type: 'background_jquery_ajax', data: { type: 'GET', url: accountSwitchUrl } })
      });
    });

    return true;
  }

  getAccounts(callback) {
    chrome.runtime.sendMessage({
        type: 'storage_get',
        data: ['accounts', 'current_account']
      },
      (items) => {
        let result = null;
        if (items.hasOwnProperty('accounts')){
          result = {
            accounts: items.accounts,
            current_account: items.current_account
          };
        } else {
          result = {
            accounts: [],
            current_account: null
          };
        }
        callback(result);
      }
    );
  }

  currentAccount(callback) {
    this.getAccounts(
      (result) => {
        let account = null;
        if (result.current_account !== null) {
          account = result.accounts[result.current_account];
        }
        callback(account);
      }
    );
  }

  // Retrieve the API key, if present
  getAPIKey(callback) {
    this.getAccounts(
      (result) => {
        let key = null;
        if (result.current_account !== null) {
          key = result.accounts[result.current_account].token;
        }

        callback(key);
      }
    );
  }

  // Post a message to the iframe if the user is logged in or not.
  // Also sends the list of accounts
  loginStatus() {
    self = this;
    this.getAccounts((result) => {
      let currentAccount = result.accounts[result.current_account];
      let loggedIn = (result.current_account !== null && currentAccount.extension_enabled != null); // eslint-disable-line

      if (this.accountSynced && loggedIn) {
        self.postMessageToIframeWindow({
          method: 'login-status',
          loggedIn: loggedIn,
          accounts: result.accounts,
          currentAccount: result.current_account
        }, '*');
      } else {
        let successCallback = (data) => {
          chrome.runtime.sendMessage(
            { type: 'storage_set', data: { accounts: data.accounts, current_account: 0 } },
            () => {
              self.postMessageToIframeWindow({ method: 'login-status', loggedIn: true, currentAccount: 0, accounts: data.accounts }, '*');
              Litmus.goToPage('checklist');
              this.accountSynced = true;
            }
          );
        };
        let errorCallback = () => {
          self.postMessageToIframeWindow({
            method: 'login-status',
            loggedIn: !!currentAccount,
            accounts: result.accounts,
            currentAccount: currentAccount ? result.current_account : null
          }, '*');
          this.accountSynced = true;
        };

        Litmus.checkLoginState(successCallback, errorCallback);
      }
    });
  }
}

module.exports = new Accounts();



