const jQuery = require('jquery');

class MandrillProvider {

  constructor() {
    this.editable = false;
  }

  lookup(options = {}) {
    let subject   = jQuery('input#subject').val();
    let content   = jQuery('textarea#html').val();
    let text      = jQuery('textarea#text').val();
    let fromName  = jQuery('input#from_name').val();
    let fromEmail = jQuery('input#from_email').val();

    let result = {subject, content, text, fromName, fromEmail};

    window.postMessage({
      method: 'checklist-lookupResult',
      result: result,
      testType: (options.testType || 'checklist')
    }, '*');
  }

}

window.ChecklistProvider = new MandrillProvider();



