const jQuery = require('jquery');
let Provider = require("../provider");

class PardotProvider {
  constructor() {
    this.editable = false;
  }

  id() {
    let matchData = window.location.href.match(/\.*id\/(\d+)/);
    if (matchData !== null) {
      return matchData[1];
    } else {
      return null;
    }
  }

  lookup(options = {}) {
    this._fetchSendDetailsJqueryObject()
      .then((jQueryObject) => {
        this.getContent(options, this._emailDetails(jQueryObject));
      });
  }

  _fetchSendDetailsJqueryObject() {
    return new Promise((resolve, reject) => {
      if (document.getElementById('subject_a')) {
        // We're in a page that has the subject available to read and we
        // can fetch it directly
        return resolve(jQuery('body'));
      } else {
        // We have to fetch a page that does contain the subject
        let sendUrl = window.location.href.replace(/(edit|test)/, 'send');
        fetch(sendUrl)
          .then((response) => {
            return response.text();
          })
          .then((htmlText) => {
            return resolve(jQuery(htmlText));
          });
      }
    });
  }

  _emailDetails(jQueryObject) {
    return {
      providerType: Provider.pardotName(),
      content: null,
      text: null,
      subject: jQueryObject.find('#subject_a').val(),
      fromName:  null,
      fromEmail: null,
      id: this.id()
    };
  }

  getContent(options, result) {
    window.postMessage({
      method: 'checklist-lookupResult',
      result: result,
      testType: (options.testType || 'checklist')
    }, '*');
  }
}

window.ChecklistProvider = new PardotProvider();



