const jQuery = require('jquery');
import {ApiService} from './services/api_service';
import EnvironmentWrapper from '../common/environmentWrapper';

const LITMUS_ENV_NAME = typeof ENV_NAME !== 'undefined' ? ENV_NAME : null;
const LITMUS_ENV_HOST = typeof ENV_HOST !== 'undefined' ? ENV_HOST : null;

class Litmus {
  constructor() {
    this.ENV = new EnvironmentWrapper(LITMUS_ENV_NAME, LITMUS_ENV_HOST);

    this.ESPS                 = [];
    this.ESPS_LOADED          = false;
    this.CURRENT_ESP          = null;
    this.PROTO                = 'https://';

    // Whether to load providers from litmus, or locally from extension
    this.LOCAL                = true;
  }

  host() {
    return this.ENV.host;
  }

  baseUrl() {
    return `${this.PROTO}${this.host()}`;
  }

  apiService() {
    if( this.externalApiService === undefined ){
      this.externalApiService = new ApiService(this.PROTO, this.host(), this.baseUrl());
    }

    return this.externalApiService;
  }

  goToPage(pagename) {
    if (window !== window.top) {
      window.location.hash = pagename;
    }
  }

  persistMfaToken(token) {
    chrome.runtime.sendMessage({
      type: 'storage_set',
      data: { mfaToken: token }
    }, () => {});
  }

  removeMfaToken() {
    chrome.runtime.sendMessage({
      type: 'storage_set',
      data: { mfaToken: null }
    }, () => {});
  }

  getChecklistResults(data, successCallback, errorCallback, authToken = null) {
    this.apiService().getESPResults(data, successCallback, errorCallback, authToken, this.CURRENT_ESP);
  }

  getInstantAPIResults(data, successCallback, errorCallback, authToken = null) {
    this.apiService().getLocalFileTestResults(data, successCallback, errorCallback, authToken);
  }

  dismissFileAccessNotice(successCallback = null, errorCallback = null) {
    this.apiService().dismissFileAccessNotice(successCallback, errorCallback)
  }

  login(username, password, successCallback, errorCallback) {
    this.apiService().login(username, password, successCallback, errorCallback);
  }

  logout(successCallback, errorCallback) {
    const logout_url = `${this.PROTO}${this.host()}/logout`;

    jQuery.ajax({
      type: 'GET',
      url: logout_url,
      headers: { 'X-Requested-With': 'XMLHttpRequest' },
      success: successCallback,
      error: errorCallback
    });
  }

  clearSession() {
    chrome.runtime.sendMessage({
      type: 'clear_session',
      data: { url: this.baseUrl() }
    }, () => {});
  }

  checkLoginState(successCallback, errorCallback) {
    this.apiService().checkLoginState(successCallback, errorCallback);
  }

  // requests an sms key to be sent to user's email address
  requestSmsToken(token, successCallback, errorCallback) {
    let url = `${this.PROTO}api.${this.host()}/v2/mfa_verification/new`;

    jQuery.ajax({
      type: 'GET',
      url: url,
      headers: { 'Authorization': 'Basic ' + btoa(token + ':') },
      success: successCallback,
      error: errorCallback
    });
  }

  createMfa(authToken, mfaData, successCallback, errorCallback) {
    let url = `${this.PROTO}api.${this.host()}/v2/mfa_setup`;

    jQuery.ajax({
      type: 'POST',
      url: url,
      data: mfaData,
      headers: { 'Authorization': 'Basic ' + btoa(authToken + ':') },
      success: successCallback,
      error: errorCallback
    });
  }

  // validates the mfa token and handles response
  validateMfaToken(authToken, mfaData, successCallback, errorCallback) {
    let url = `${this.PROTO}api.${this.host()}/v2/mfa_verification`;

    jQuery.ajax({
      type: 'POST',
      url: url,
      data: mfaData,
      headers: { 'Authorization': 'Basic ' + btoa(authToken + ':') },
      success: successCallback,
      error: errorCallback
    });
  }


  // Request rendering of email screenshots
  //
  // this.example Request screenshots
  //   Litmus.renderScreenshots 'testId', (success) ->
  //     console.log 'rendering requested'
  requestScreenshots(token, testId, successCallback, errorCallback) {
    this.apiService().requestESPScreenshots(token, testId, successCallback, errorCallback)
  }

  requestInstantAPIScreenshots(token, guid, pusher_channel, callback) {
    // this method should no longer be called when logged out or in a non-paid plan,
    // since free plans are depreciated and we now show a cta page instead.  Return early if
    // something slips through the cracks
    if (token === null) {
      return;
    }

    let headers   =   {};
    let url       = null;
    let tokenHash = null;

    chrome.runtime.sendMessage({
        type: 'storage_get',
        data: ['mfaToken']
      },
      ((items) => {

        url = `${this.baseUrl()}/checklist/external_api/local_tests/${guid}/render.json`;
        tokenHash = btoa(token + ':');
        headers['Authorization'] = `Basic ${tokenHash}`;
        headers['X-Litmus-OTP'] = items.mfaToken;

        jQuery.ajax({
          type: 'PUT',
          url: url,
          data: {
            pusher_channel: pusher_channel
          },
          dataType: 'json',
          headers: headers,
          success: callback
        });
      })
    );
  }

  getCurrentClientResult(token, code, testId, success, error) {
    if (token === null) {
      error();
      return;
    }

    let headers   =   {};
    let url       = null;
    let tokenHash = null;

    chrome.runtime.sendMessage({
        type: 'storage_get',
        data: ['mfaToken']
      },
      ((items) => {
        url = `${this.baseUrl()}/checklist/external_api/tests/${testId}/retest.json`;
        tokenHash = btoa(token + ':');
        headers['Authorization'] = `Basic ${tokenHash}`;
        headers['X-Litmus-OTP'] = items.mfaToken;

        jQuery.ajax({
          type: 'POST',
          dataType: 'json',
          data: {
            code: code
          },
          url: url,
          headers: headers,
          success: function(data) {
            success(data, code);
          },
          error: error
        });
      })
    );
  }

  getTestLink(testId) {
    return `${this.baseUrl()}/checklist/tests/${testId}`;
  }

  getCatalystChecklistLink(folderId, emailId) {
    return `${this.baseUrl()}/folders/${folderId}/emails/${emailId}/checklist`;
  }
}

module.exports = new Litmus();



