class GmailProvider {

  constructor() {
    this.editable = false;
  }

  lookup(options = {}) {
    let content   = jQuery('.a3s').html();
    let subject   = jQuery('.hP:visible').text();
    let fromName  = jQuery('h3.iw:visible:first .gD').text();
    let fromEmail = jQuery('h3.iw:visible:first .gD').attr('email');

    let result = {
      subject: subject,
      content: content,
      text: null,
      fromName: fromName,
      fromEmail: fromEmail
    };

    window.postMessage({
      method: 'checklist-lookupResult',
      result: result,
      testType: (options.testType || 'checklist')
    }, '*');
  }

}

window.ChecklistProvider = new GmailProvider();



