const jQuery = require('jquery');

class ConstantContactProvider {

  constructor() {
    this.editable = false;
  }

  getParameterByName(name) {
    name = name.replace(/[\\[]/, '\\\\[').replace(/[\\]]/, '\\\\]');
    regex = new RegExp('[\\\\?&]' + name + '=([^&#]*)');
    let results = regex.exec(location.search);
    if(results && results[1]) {
      return decodeURIComponent(results[1].replace(/\\+/g, ' '));
    } else {
      return null;
    }
  }

  getUidFromPreviewImageUrl() {
    let srcUrl = jQuery('img#previewImage').attr('src');
    let match  = srcUrl.match(/.\/activity\/(d+)\/./);

    if(match !== null) {
      return match[0];
    } else {
      return null;
    }
  }

  getUidFromPrevPage(callback) {
    jQuery.get(window.location).success( (data) => {
      let uid = jQuery(data).find('input[name="agent.uid"]').val();
      return callback(uid);
    });
  }

  lookup(options = {}) {

    if( window.location.pathname.match(/rnavmap\/evaluate\.rnav/) !== null) {
      this.lookupCustomCodeEmailCampaign(options);
    }

    if (window.location.pathname.match(/rnavmap\/emcf\/email/) !== null) {
      this.lookupNewsletterEmailCampaign(options);
    }

  }

  lookupCustomCodeEmailCampaign(options) {
    const uid = jQuery('input[name="agent.uid"]').val() || this.getParameterByName('agent.uid');

    let result     = {};
    let subject    = jQuery('input[name  = "LETTER.SUBJECT"]').val();
    let fromEmail  = jQuery('select[name = "LETTER.FROM.EMAIL"] option[selected]').text();
    let replyEmail = jQuery('select[name = "LETTER.REPLY.EMAIL"] option[selected]').text();
    let fromName   = jQuery('input[name  = "LETTER.FROM.NAME"]').val();

    let popupHTMLPreviewUrl = `https://ui.constantcontact.com/templates/previewer.jsp?format=html&agent.uid=${uid}`;
    let popupTextPreviewUrl = `https://ui.constantcontact.com/templates/previewer.jsp?format=txt&agent.uid=${uid}`;
    jQuery.get(popupHTMLPreviewUrl).pipe((popupHtmlData) => {
      let $popupHTML = jQuery(popupHtmlData);
      let foundBRTag = false;
      let filteredData = [];
      for(let i = 0; i < $popupHTML.length; i++) {
        if(!foundBRTag) {
          if($popupHTML[i].localName === 'br') {
            foundBRTag = true;
          }
        } else {
          filteredData.push($popupHTML[i]);
        }
      }
      let htmlData = '';
      jQuery(filteredData).each(function(index, item){
        if (jQuery(item).html()) {
          htmlData += jQuery(item).prop('outerHTML');
        }
      });

      let $previewPage = jQuery(popupHtmlData);
      let firstTrText  = $previewPage.find('.previewHeaderTable tr:first td.previewHeaderValue').text();
      let secondTrText =  $previewPage.find('.previewHeaderTable tr:nth-child(2) td.previewHeaderValue').text();

      let name, email;
      let fromRegex = /(.*)<(.*@.*)>/;
      let match     = fromRegex.exec(firstTrText.trim());
      if (match !== null) {
        email = match[2];

        if (!fromName) {
          fromName = match[1] && match[1].trim();
        }
      }

      if(!subject) {
        subject = secondTrText.replace('Subject:', '');
      }


      if(!fromEmail) {
        fromEmail = email;
      }

      result = {
        content: htmlData,
        subject: subject,
        fromName: fromName,
        fromEmail: fromEmail
      };
      return jQuery.get(popupTextPreviewUrl);
    }).pipe((popupTextData) => {
      let text = jQuery(popupTextData).find('pre').text();
      result.text = text;
      window.postMessage({
        method: 'checklist-lookupResult',
        result: result,
        testType: (options.testType || 'checklist')
      }, '*');
    }).fail(() => {
      result.content = ' ';
      result.text    = ' ';

      window.postMessage({
        method: 'checklist-lookupResult',
        result: result,
        testType: (options.testType || 'checklist')
      }, '*');
    });

  }

  lookupNewsletterEmailCampaign(options) {
    const processPreviewPage = (uid) => {
        let result = null;
        let subject    = jQuery('input#subject').val();
        let fromEmail  = jQuery('from-email-dropdown').text();

        let replyEmail  = jQuery('reply-email-dropdown').text();
        let fromName   = jQuery('input#fromName').val();

        previewURL = `/visualeditor/visual_editor_preview.jsp?agent.uid=${uid}`;

        jQuery.get(previewURL, (data) => {
          let htmlData = jQuery(data);
          let htmlMatches = data.match(/<html\b[^>]*>((.|[\n\r])*)<\/html>/i);
          if(!subject) {
            subject = htmlData.find('td strong:contains("Subject:")')
                          .closest('tr')
                          .find('td').last().text().trim();

          }

          if(!fromEmail) {
            fromEmail = htmlData.find('td strong:contains("From:")')
                                  .closest('tr')
                                  .find('td').last().text().trim();
          }
          if(!fromName) {
            let nameEmailMatches = fromEmail.match(/([^<]+)<([^>]+)>/);
            if (nameEmailMatches) {
              fromName  = nameEmailMatches[1].trim();
              // fromEmail = nameEmailMatches[2].trim();
            }

          }

          if (!replyEmail || replyEmail === '') {
            /// Always set fromEmail to reply email for now.
            replyEmail = htmlData.find('td strong:contains("Reply:")')
                                     .closest('tr')
                                     .find('td').last().text().trim();
            fromEmail = replyEmail;
          }

          let html = htmlMatches !== null ? htmlMatches[0] : ' ';

          // Preview popup injects some junk at the bottom of the email, including a hidden form.
          html = html.replace(/<div id="leftActions"[\s\S]*<\/body>/, '</body>');

          // Remove header table with subject/from/reply-to details
          html = html.replace(/<table[^>]+class="header-info">[\s\S]*?<\/table>/, '');

          result = {
            content: html,
            text: '',
            subject: subject,
            fromName: fromName,
            fromEmail: fromEmail
          };

          window.postMessage({
            method: 'checklist-lookupResult',
            result: result,
            testType: (options.testType || 'checklist')
          }, '*');
        })
        .error(function(err){
          // empty html content for fallback
          result = {
            content: ' ',
            text: '',
            subject: subject,
            fromName: fromName,
            fromEmail: fromEmail
          };

          window.postMessage({
            method: 'checklist-lookupResult',
            result: result,
            testType: (options.testType || 'checklist')
          }, '*');
        });
    };

    let uid = jQuery('input[name="agent.uid"]').val() || this.getParameterByName('agent.uid');

    if(uid) {
      processPreviewPage(uid);
    } else {
      this.getUidFromPrevPage(processPreviewPage);
    }
  }

  // Easiest way to fix the body width, since they have a fixed position sidebar on the left
  opened() {
    jQuery('body').css('width', 'calc(100% - 470px - 90px)');
  }

  closed() {
    jQuery('body').css('width', 'calc(100% - 10px - 90px)');
  }

  injectCSS() {
    jQuery('body').append(`
      <style>
        html.litmus-checklist-expanded body { margin-left: 0; }
        html.litmus-checklist-expanded header.ToolkitHeader { width: calc(100% - 470px) }
      </style>
    `);
  }

  id() {
    let uid = jQuery('input[name="agent.uid"]').val() || this.getParameterByName('agent.uid');

    if(uid) {
      return processPreviewPage(uid);
    } else {
      return this.getUidFromPrevPage(processPreviewPage);
    }
  }

}

window.ChecklistProvider = new ConstantContactProvider();



