let Provider = require("../provider");

class IbmProvider {

  constructor() {
    this.editable = false;
  }

  lookup(options = {}) {
    let result = {
      providerType: Provider.ibmName()
    };

    window.postMessage({
      method: 'checklist-lookupResult',
      result: result,
      testType: (options.testType || 'checklist')
    }, '*');
  }
}

window.ChecklistProvider = new IbmProvider();



