import jQuery from 'jquery';
let Provider = require("../provider");
const debounce = require('js-debounce');
const Utils  = require('../../chrome/inject/modules/utils.js');

class EloquaProvider {

  constructor() {
    this.editable       = false;
    this.onEmailPage    = false;
    this.debounceDelay  = 1500;
    this.rendered       = false;
    this.onDashboard    = false;
    this.currentMarkup  = null;
    this.currentId      = null;

    this.initHashChangeListener();
  }

  lookup(options = {}) {
    this.rendered = false;
    this.currentMarkup = null;
    // manual check in case we open the plugin on an already-loaded page
    // delayed so that it won't interfere with the observer
    setTimeout(() => {
      this.handleCurrentPage();
    }, this.debounceDelay);

    var observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.addedNodes && mutation.addedNodes.length > 0) {
          this.handleCurrentPage();
        }
      });
    });

    var config = {
        attributes: true,
        childList: true,
        characterData: true,
        subtree: true
    };

    observer.observe(document.body, config);
  }

  initHashChangeListener() {
    jQuery(window).on('hashchange', () => {
      this.currentMarkup = null;
      if(window.location.hash.match(/emails&id=(.*)/)) {
        this.onEmailPage = true;
      } else {
        this.onEmailPage = false;
      }
    });
  }

  initCurrentMarkupListener() {
    let timer = setInterval(() => {
      if (this.currentMarkup) {
        Utils.postMessageToIframeWindow({method: 'checklist-showRetestButton' }, '*');
      } else {
        Utils.postMessageToIframeWindow({method: 'checklist-hideRetestButton' }, '*');
        clearInterval(timer);
      }
    }, 1000);
  }

  handleCurrentPage() {
    debounce( 'handleCurrentPage', this.debounceDelay, () => {
      if(this.onEmailPage) {
        let id = window.location.href.match(/id=(.*)/)[1];

        // if coming from dashboard, fetch inital content if initial html is blank
        if (this.onDashboard) {
          Utils.postMessageToIframeWindow({method: 'checklist-show-checklist' }, '*');
          if (!this.currentMarkup) {
            this.lookup();
            Utils.postMessageToIframeWindow({method: 'checklist-hideRetestButton' }, '*');
          } else {
            Utils.postMessageToIframeWindow({method: 'checklist-showRetestButton' }, '*');
          }

        } else if(!this.currentMarkup) {
          let result = {
            providerType: Provider.eloquaName()
          };

          window.postMessage({
            method: 'checklist-lookupResult',
            result: result,
            testType: 'checklist'
          }, '*');

          this.initCurrentMarkupListener();
        }

        // show retest button if we've changed to a different email
        if (this.currentId !== id) {
          Utils.postMessageToIframeWindow({method: 'checklist-showRetestButton' }, '*');
        }

        this.onDashboard = false;
        this.currentId   = id;
      } else  {
        Utils.postMessageToIframeWindow({method: 'checklist-handle-unsupported-dashboard' }, '*');
        this.onDashboard = true;
      }
      this.rendered = true;
    });
  }

  id() {
    let matchData = window.location.href.match(/emails\&id=(\d+)/);
    if (matchData !== null) {
      return matchData[1];
    }
  }
}

window.ChecklistProvider = new EloquaProvider();



