const jQuery = require('jquery');

export default class MailchimpProvider {
  constructor() {
    this.editable = false;
    this.id = this.getTemplateId;
  }

  getParameterByName(name) {
    name = name.replace(/[\\[]/, '\\\\[').replace(/[\\]]/, '\\\\]');
    let regex = new RegExp('[\\\\?&]' + name + '=([^&#]*)');
    let results = regex.exec(location.search);
    return decodeURIComponent(results[1].replace(/\\+/g, ' '));
  }

  urlForPath(path) {
    let prefix = window.location.origin + '/campaigns/';
    let campaignId = this.getParameterByName('id');
    return `${prefix}${path}?id=${campaignId}`;
  }

  getTemplateId() {
    let matchData = window.location.search.match(/\?.*id=(\d+)/);
    if (matchData !== null) {
      return matchData[1];
    } else {
      return null;
    }
  }

  isOnTemplatePath() {
    let match = window.location.pathname.match(/templates\/(edit|design)/);
    return match !== null;
  }

  lookup(options = {}) {
    if (this.isOnTemplatePath()) {
      this.templateLookup(options);
    } else {
      this.normalLookup(options);
    }
  }

  templateLookup(options) {
    let templateId = this.getTemplateId();
    jQuery.get(`/templates/preview?id=${templateId}`).done((html) => {
      let result = {
            content:   html,
            text:      ' ',
            subject:   ' ',
            fromName:  ' ',
            fromEmail: ' ',
            id: templateId
          };
       window.postMessage({
            method: 'checklist-lookupResult',
            result: result,
            testType: (options.testType || 'checklist')
          }, '*');
       window.postMessage({method: 'checklist-skipFirstImpressionsSection'}, '*');
    });
  }

  normalLookup(options) {
    let subject     = null,
        fromName    = null,
        fromEmail   = null,
        $fromData   = null;

    if (jQuery('input[name="content[subject]"]').length) {
      subject = jQuery('input[name="content[subject]"]').val();
    }

    if (jQuery('input[name="content[from_name]"]').length) {
      fromName = jQuery('input[name="content[from_name]"]').val();
    }

    if (jQuery('input[name="content[from_email]"]').length) {
      fromEmail = jQuery('input[name="content[from_email]"]').val();
    }

    jQuery.get(this.urlForPath('wizard/setup')).done((fromData) => {
      let $fromData = jQuery(fromData);
      if(fromName === null) {
        fromName  = $fromData.find('#from-name').val();
      }
      if (fromEmail === null) {
        fromEmail = $fromData.find('#from-email').val();
      }

      if (subject === null) {
        subject   = $fromData.find('#subject').val();
      }

      // Sample response: {"member_id":79809301,"email":"kevin@litmus.com","is_last":true}
      jQuery.get(this.urlForPath('preview-recipient') + '&pos=0').done((recipient) => {
        jQuery.get(this.urlForPath('preview-content-html') + '&e=' + recipient.member_id).done((html) => {

          let result = {
            content:   html,
            text:      '',
            subject:   subject,
            fromName:  fromName,
            fromEmail: fromEmail,
            id: this.id()
          };

          window.postMessage({
            method: 'checklist-lookupResult',
            result: result,
            testType: (options.testType || 'checklist')
          }, '*');
        });
      });
    });
  }

  injectCSS() {
    jQuery('body').append(`
      <style id='checklist-injected-css'>

        #paste-code > .template-meta, #import-file > .template-meta, #import-url > .template-meta {
          padding-bottom: 100%;
        }

        #import-zip {
          left: 200px!important;
          top: 50px!important;
        }

        .topsliderpane-container.filemanager-container {
          width: inherit;
        }

        .dijitDialog {
          left: 200px!important;
        }
      </style>
    `);
  }

  getAccountInfo(callback) {
    let accountUrl = window.location.origin + '/account/profile/';
    jQuery.get(accountUrl).done( (page) => {
      let $page = jQuery(page);
      let username = null;
      let email    = null;
      let $usernameInput = $page.find('#username');
      if ($usernameInput) {
        username = $usernameInput.val();
      }
      let $emailInput = $page.find('#email');
      if ($emailInput) {
        email = $emailInput.val();
      }
      callback({name: 'MailChimp', username: username, email: email, campaign_url: `${window.location.origin}/campaigns`});
    })
    .fail( (error) => {
      //console.log("Failed to Retrieve Account Page!!!", accountUrl);
    });
  }
}

window.ChecklistProvider = new MailchimpProvider();



